import './App.css';
import { Route } from 'react-router';
import Landing from './components/Landing/Landing.js';
import Home from './components/Home/Home.js';
import AddActivity from './actions/addActivity/addActivity.js';
import GetCountry from './actions/getByName/getByName.js';
import Pagination from './actions/pagination/pagination.js';

function App() {
  return (
    <div className="App">
      <Route path = '/' exact component = {Landing}/>
      <Route path = '/countries' exact component = {Home}/>
      <Route path = '/countries/:name' exact component = {GetCountry}/>
      <Route path = '/addActivity' exact component = {AddActivity}/>
      <Route path = '/showAll' exact component = {Pagination}/>
    </div>
  );
}

export default App;
